@font-face {
  font-family: 'icons';
  src:  url('../fonts/icons.eot?60ovxc');
  src:  url('../fonts/icons.eot?60ovxc#iefix') format('embedded-opentype'),
    url('../fonts/icons.ttf?60ovxc') format('truetype'),
    url('../fonts/icons.woff?60ovxc') format('woff'),
    url('../fonts/icons.svg?60ovxc#icons') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icons' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

